import {WiCloud, WiRain, WiSnow, WiSolarEclipse} from "react-icons/wi"; 

const weather = {
    getWeatherIcon(weather, size){
        switch(weather){
            case 'Clouds':
                return <WiCloud size={size}/>
            case 'Snow':
                return <WiSnow size={size}/>
            case 'Rain':
                return <WiRain size={size}/>
            case 'Clear':
                return <WiSolarEclipse size={size}/>
            default:
                return 'No available icon'
        }
    }
}


export default weather