const days = {
    getDayOfWeek(date){
        var d = new Date(date).getDay()
        var days = ['Pondl', 'ter', 'Steda', 'tvrtek', 'Ptek', 'Sobota', 'Nedle']
        return days[d]
    },

    formatDate(date){
        var d = new Date(date),
            month = '' + (d.getMonth() + 1),
            day = '' + d.getDate(),
            year = d.getFullYear();
    
        if (month.length < 2) 
            month = '0' + month;
        if (day.length < 2) 
            day = '0' + day;
    
        return [day, month, year].join('.');
    }
}


export default days