import React from 'react';
import axios from 'axios';
import { useState, useEffect } from 'react';
import ReactLoading from 'react-loading';
import days from '../functions.js/days';
import weather from '../functions.js/weather';

const Homepage = () => {
    const [currentloc, setCurrentLoc] = useState('4eb72ff9-c1a2-4c1d-82a3-35c894c2ce02')
    const [fetched, setFetched] = useState(false);
    const [weatherData, setWeatherData] = useState([])
    const [locations, setLocations] = useState([])

    useEffect(() => {
        axios.get('/locations')
        .then((resp)=>{
            setLocations(resp.data)
        })
        axios.get('/weather?location='+ currentloc)
            .then((resp)=>{
                setWeatherData(resp.data.data);
                setFetched(true)
            })
    }, [currentloc])

    const change = event => {
        setCurrentLoc(event.target.value)
    }
    

    return (
        <div>
            <div className='my-5'>
                <div className='px-10'>
                    <div>
                        {fetched ?
                        <div className='grid place-items-center'>
                            <div className='p-5 rounded-xl shadow-xl bg-white'>
                                <div className='px-2 mb-4 flex justify-between'>
                                    <div>
                                        <select name="location" id="locations" onChange={change} className='font-bold rounded text-xl mb-2 outline-none'>
                                            {locations.map((item) => (
                                                <option value={item.locationId}>{item.name}</option>
                                            ))}
                                        </select>
                                        <p className='font-bold text-2xl mb-2'>{Math.round(weatherData.current.temp)} C</p>
                                    </div>
                                    <div>
                                        {weather.getWeatherIcon(weatherData.current.weather, 80)}
                                    </div>
                                </div>
                                <div className='flex-column md:grid gap-3 grid-cols-7 mb-5 md:text-center'>
                                    {weatherData.forecast.slice(0,-1).map((item)=>(
                                        <div className='grid grid-cols-3 pl-2 gap-3 md:block' key={item.id}>
                                            <p className='self-center text-md font-semibold mb-2'>{days.getDayOfWeek(item.date)}</p>
                                            <p className='xl:ml-2 mb-2'>{weather.getWeatherIcon(item.weather, 40)}</p>
                                            <p className='self-center'>{Math.round(item.day_temp)} C</p>
                                        </div>
                                    ))}
                                </div>
                            </div>
                        </div>
                        :
                        <div className='grid h-screen place-items-center'>
                            <ReactLoading type="spin" color="#0000FF" height={100} width={50} />
                        </div>
                        }
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Homepage